import React from "react"
// import PropTypes from "prop-types"
import Astronaut from "./Astronaut"
import { Trail, animated, config } from 'react-spring/renderprops'

export default class Astronauts extends React.Component {
//   constructor(props) {
//     super(props);
//   }

  render () {
    return (
      <div>
        { this.props.astros
            ?
            <>
                {/* <p>{this.props.astros.number}</p> */}
                <ul className="astronauts">
                    <Trail
                        native
                        config={config.gentle}
                        items={this.props.astros.people}
                        keys={ item => item.name }
                        from={{ opacity: 0}}
                        to={{ opacity: 1 }}>
                        {item => props => (
                        <animated.li
                            style={props}
                            className="astronaut"
                        >
                            <Astronaut
                                name={ item.name }
                                craft={ item.craft }
                            />
                        </animated.li>
                    )}
                    </Trail>
                </ul>
            </>
            :
            <div>Loading Astronauts</div>
        }
      </div>
    )
  }
}
